import { loglevel } from "./ILogLevel";

export interface IModelInfo {
    id: string;
    name: string;
    version: string;
}
export interface IEditorInfo {
    editor: "quick" | "flow" | "process";
    version: string;
    innerEditor?: IEditorInfo;
}

export interface IOwnerInfo {
    compID?: string;
    compType?: string;
    event?: string;
    property?: string;
    stackTrace?: string;
    msg?: string;
}

export interface IUserAgentInfo {
    userAgentString: string;
}
export interface IEnvironmentInfo {
    cpuArch: string;
    os: string;
    nodeVersion: string;
}
export interface IBaseMsg {
    msg: string;
    type: string;
    userAgent?: IUserAgentInfo;
}


export interface IBaseErrorMsg extends IBaseMsg {      
    environment?: IEnvironmentInfo;
}
